*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  box-sizing: border-box;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Black.ttf");
  font-style: normal;
  font-weight: 900;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Bold.ttf");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.ttf");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Italic.ttf");
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Thin.ttf");
  font-style: italic;
  font-weight: 300;
}
h2 {
  font-family: "Lato";
  font-weight: 700;
  font-size: 2rem;
}

h3 {
  font-family: "Lato";
  font-weight: 700;
  font-size: 1.5rem;
}

h4 {
  font-family: "Lato";
  font-weight: 400;
  font-size: 1.3rem;
}

.projects {
  background-color: #fafafa;
  box-shadow: 0rem 1rem 0.5rem rgba(63, 63, 70, 0.3);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.3rem;
}
.projects > *:not(:last-child) {
  margin-bottom: 2rem;
}
.projects__list {
  height: 80vh;
  overflow-y: auto;
}
.projects__list > *:not(:last-child) {
  margin-bottom: 2rem;
}
.projects__project {
  background-color: #e5e5e5;
  display: grid;
  grid-template-rows: 4rem 4rem 4rem;
  grid-gap: 2rem;
  justify-items: center;
  align-items: center;
  padding: 1rem;
  border-radius: 0.3rem;
  box-shadow: 0rem 0.5rem 0.5rem rgba(63, 63, 70, 0.3);
}

.todos {
  background-color: #fafafa;
  box-shadow: 0rem 1rem 0.5rem rgba(63, 63, 70, 0.3);
  padding: 2rem;
  border-radius: 0.3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.todos__title {
  margin-bottom: 2rem;
}
.todos__list {
  display: flex;
  flex-direction: column;
  height: 80vh;
  width: 100%;
  overflow-y: auto;
}
.todos__list > *:not(:last-child) {
  margin-bottom: 2rem;
}

.todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
  padding: 2rem;
  background-color: #e5e5e5;
}
.todo:nth-child(even) {
  background-color: #f3f1f1;
}

.project__head {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.project__controls > * {
  display: inline-block;
  font-size: 1.5rem;
}
.project__description {
  text-align: center;
}

.header {
  background-color: #18181b;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f5f5f5;
  font-family: "Lato";
  font-weight: 900;
  font-size: 2rem;
}

.app {
  width: 120rem;
  align-self: center;
  min-height: 80vh;
  display: grid;
  grid-template-columns: 30rem 1fr;
  grid-column-gap: 2rem;
  justify-self: center;
}

.footer {
  background-color: #18181b;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f5f5f5;
  font-family: "Lato";
  font-weight: 900;
  font-size: 2rem;
}

.content {
  background-color: #f5f5f5;
  min-height: 100vh;
  width: 100%;
  display: grid;
  grid-template-rows: 4rem 1fr 4rem;
}